$(function () {
    $(document).on("click", "#lastBtn", function (e) {
        history.go(-1);
        e.preventDefault();
        return false;
    });
    
});

angular.module("MyApp", ["PageM","ToolM", "cfp.hotkeys"])
         .controller("MyController", ["$scope", "PageProvider", "ToolProvider", "hotkeys", function ($scope, PageProvider,TPM, hotkeys) {

             $scope.PM = PageProvider;
             $scope.TM = {};
             $scope.VM = {};
             $scope.VM.enState = "0";
             $scope.VM.title = "";
             $scope.VM.publishState = "0";

             $scope.changePage = function (pageIndex) {
                 $("#tmBlock").show();
                 var pgData = $scope.PM.genPageData(pageIndex);
                 var postData = {};
                 postData["i"] = I;
                 postData["en"] = $scope.VM.enState;
                 postData = $scope.PM.filterPageData(pgData, postData);
                 $scope.PM.changePage("/System/CodeM/CodeSetting_DetailOP.aspx", postData, function (data) {
                     $scope.TM.data = data.message;
                     $scope.$apply(function () {});
                 });
             };

             $scope.changePage(1);

             hotkeys.add({
                 combo: 'enter',
                 description: 'Description goes here',
                 allowIn: ['INPUT',"BUTTON", 'SELECT', 'TEXTAREA'],
                 callback: function (event, hotkey) {
                     var focusType = document.activeElement.type;
                     if (focusType == "text" || focusType == "select-one")
                     {
                         //$scope.changePage(1);
                         //event.preventDefault();
                         //return false;
                         //$("input[name='addBtn']").trigger("click");
                     }
                 }
             });

             $scope.goAdd = function () {
                 var obj = {};
                 obj['ii'] = $('#i').val();
                 TPM.doPOST("/System/CodeM/CodeSetting_Add.aspx",obj,document.querySelector("#MyForm"));
             };

             $scope.goEdit = function (record) {
                 var obj = {};
                 obj['iii'] = record['I'];
                 TPM.doPOST("/System/CodeM/CodeSetting_DetailMaintain.aspx", obj, document.querySelector("#MyForm"));
             };

}]);

